
.move-enter-active,
.move-leave-active {
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
.move-enter,
.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
  opacity: 1;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.loader-enter-active,
.loader-leave-active {
  -webkit-transition: opacity 0.5s ease-out;
  transition: opacity 0.5s ease-out;
  opacity: 1;
}
.loader-enter,
.loader-leave-active {
  opacity: 0;
}
.slideDown-enter-active,
.slideDown-leave-active {
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}
.slideDown-enter,
.slideDown-leave-active {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.overflow-text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}
.app-footer {
  font-size: 0;
  position: fixed;
  bottom: 0;
  padding-top: 3px;
  background: #fff;
  left: 0;
  width: 100%;
  border-top: 1px solid #ddd;
}
.footer-item {
  font-size: 0.85rem;
  width: 33.33%;
  height: 100%;
  display: inline-block;
  text-align: center;
  padding-top: 28px;
  background-repeat: no-repeat;
  background-size: 24px 24px;
  background-position: center top;
  padding-bottom: 0.2rem;
}
.footer-item.router-link-active {
  color: #90b4fc;
}
.footer-item:nth-of-type(1) {
  background-image: url("./common/img/movie.png");
}
.footer-item:nth-of-type(1).router-link-active {
  background-image: url("./common/img/movie_active.png");
}
.footer-item:nth-of-type(2) {
  background-image: url("./common/img/cinema.png");
}
.footer-item:nth-of-type(2).router-link-active {
  background-image: url("./common/img/cinema_active.png");
}
.footer-item:nth-of-type(3) {
  background-image: url("./common/img/user.png");
}
.footer-item:nth-of-type(3).router-link-active {
  background-image: url("./common/img/user_active.png");
}


