
.my-swipe {
  color: #fff;
  font-size: 30px;
  text-align: center;
}
.swipe-item{
  background-size: cover;
}
.swipe-item a{
  display: block;
  width: 100%;
  height:100%;
}
.mint-swipe-indicator{
  opacity: 1;
  background:#fff;
}
.mint-swipe-indicator.is-active{
  background: #FCB890;
}



