
.star {
  height: 0.8rem;
  position: relative;
  font-size: 0;
}
.star .star-box {
  width: 4.1rem;
  position: absolute;
  left: 0;
  top: 0;
}
.star .star-gray {
  width: 4.1rem;
}
.star .star-gray .star-item {
  background: url("img/star48_off@3x.png") no-repeat;
  background-size: 0.8rem auto;
}
.star .star-mask {
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}
.star .star-yellow {
  height: 0.8rem;
  width: 4.1rem;
  position: absolute;
  left: 0;
  top: 0;
}
.star .star-yellow .star-item {
  background: url("img/star48_on@3x.png") no-repeat;
  background-size: 0.8rem auto;
}
.star .star-item {
  display: inline-block;
  height: 0.8rem;
  width: 0.8rem;
  background-size: 10px auto;
  background-repeat: no-repeat;
}


